import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle,BreadcrumbItem,Breadcrumb, ModalBody,Modal,ModalHeader, Row, Label, Col, Button } from 'reactstrap';
import {Link} from 'react-router-dom';
import { LocalForm,Control,Errors } from 'react-redux-form';
import {Loading} from './loading';
import { baseUrl } from '../shared/baseURLS';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

    function RenderComments({comments,dishId,postComment})
    {   if (comments != null) {
            return (
                <div className="col-12 col-md-6 m-1">
                    <h4>Comments</h4>
                    <ul className="list-unstyled">

                        <Stagger in>
                            {comments.map((comment) => {
                                return (
                                    <Fade in>
                                        <li key={comment.id}>
                                            <p>{comment.comment}</p>
                                            <p>-- {comment.author} , {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit' }).format(new Date(Date.parse(comment.date)))}</p>
                                        </li>
                                    </Fade>
                                );
                            })}
                        </Stagger>
                
                    </ul>
                    <CommentForm dishId={dishId} postComment={postComment} />
                    
                </div>
            );
        }
    }

    function RenderDish({dish}) {
       
       if (dish != null)
                return (
                    <FadeTransform
                        in
                        transformProps={{
                            exitTransform: 'scale(0.5) translateY(-50%)'
                        }}>
                        <Card>
                            <CardImg top src={baseUrl + dish.image} alt={dish.name} />
                            <CardBody>
                                <CardTitle>{dish.name}</CardTitle>
                                <CardText>{dish.description}</CardText>
                            </CardBody>
                        </Card>
                    </FadeTransform>

                );
        else
            return (
                <div></div>
            );
    }


class CommentForm extends Component{

    constructor(props) {
        super(props);
        
        this.state = {
            isModalOpen: false
        };
        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit=this.handleSubmit.bind(this);

    }

    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    handleSubmit(values) {
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
    }
    

    render(){

        const required = (val) => val && val.length;
        const maxLength = (len) => (val) => !(val) || (val.length <= len);
        const minLength = (len) => (val) => val && (val.length >= len);

        return (
            <div>
                <button  onClick={this.toggleModal} outline><i class="fa fa-pencil" aria-hidden="true">Submit Comment</i></button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                        <ModalHeader>
                            Submit Comment
                        </ModalHeader>
                        <ModalBody>
                        <LocalForm onSubmit={this.handleSubmit}>
                            <Row class="form-group">
                                <Col><strong>Rating</strong>
                                   <Control.select name="rating" model=".rating" className="form-control">
                                       <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                   </Control.select>
                                </Col>
                            </Row>
                            <Row class="form-group">
                                <Col>

                                    <Label htmlFor="author"><strong>Your Name</strong></Label>
                                    <Control.text name="author" id="author" model=".author" className="form-control" placeholder="Your Name"
                                    validators={{required,minLength:minLength(3),maxLength:maxLength(15)}} />

                                    <Errors model=".author" className="text-danger" show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 3 characters',
                                            maxLength: 'Must be 15 characters or less'
                                        }} ></Errors>
                                        
                                </Col>
                            </Row>
                            <Row class="form-group">
                                <Col>

                                    <Label htmlFor="comment"><strong>Comment</strong></Label>
                                    <Control.textarea name="comment" id="comment" model=".comment" className="form-control"  rows="6"/>

                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <Button type="submit" color="primary">
                                       Submit
                                    </Button>
                                </Col>
                            </Row>
                           </LocalForm>
                        </ModalBody>
                </Modal>
            </div>
        );
    }
}

const DishDetail= (props)=>{
    if (props.isLoading) {
        return (
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if (props.errMess) {
        return (
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    }
    else if(props.dish!=null)
                return(   
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>  
                </div>
                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish={props.dish} />
                    </div>
                    <div className="col-12 col-md-5 m-1">
                        <RenderComments comments={props.comments} postComment={props.postComment} dishId={props.dish.id}/>
                    </div> 
                </div>
            </div>
        );
}

export default DishDetail;